import React, {useState} from 'react';
import data from './css/data.css'
const sertifikat =[{
  id: 0,
  instansi: 'Codepolitan',
  sertifikat: 'codepolitan.jpg',
  keahlian: 'HTML dan CSS Dasar',
  link: 'https://www.codepolitan.com'
}, {
  id: 1,
  instansi: 'FreeCodeCamp',
  sertifikat: 'fcc.jpg',
  keahlian: 'Designer Web Responsive',
  link: 'https://www.freecodecamp.org/'
}, {
  id: 2,
  instansi: 'FreeCodeCamp',
  sertifikat: 'FCCJavascript.png',
  keahlian: 'JavaScript Algoritms and Data Structures',
  link: 'https://www.freecodecamp.org/'
}];

function ListSertifikat() {
const [listItem, setListItem] = useState(sertifikat[0]);


function handleClick() {
  if (listItem.id === 0) {
  setListItem(sertifikat[1]);
  } else if(listItem.id === 1){
  setListItem(sertifikat[2]);
  } else {
  setListItem(sertifikat[0]);
  }
}


return (
    <section>
      <div className="polaroid" key={listItem.id}>
        <img src={listItem.sertifikat} onClick={handleClick} />
        <div className="pcontent">
          <p><b>{listItem.instansi}</b></p>
          <a href={listItem.link} target="_blank">
            <p>{listItem.keahlian}</p>
          </a>
        </div>
      </div>
    </section>
  );
}
export default ListSertifikat;
